<template>
    <div class="loadingpage" v-if="show">
        <div class="loadingtitle">
            <div class="loaddot">
                <div></div>
            </div>
            <div class="loaddot"></div>
            <div class="loaddot"></div>
            <div class="loaddot"></div>
        </div>
    </div>
</template>

<script>
import gsap from "gsap";
export default {
    data() {
        return {
            show: true
        }
    },
    mounted() {
        this.repeatload();
        this.closeload();

    },
    methods: {
        ani(el, delay) {
            // let load = document.querySelector(".loaddot")
            // gsap.to(load, { rotation: 27, x: 100, duration: 1 });
            gsap.to(
                el, {
                    ease: 'easeInout', // Easing
                    translateY: -30, // Move the dot -30px on Y axis
                    yoyo: true, // If true, playback will alternate forwards and backwards on each repeat
                    repeat: -1, // how many times the animation should repeat (-1 is infinite)
                    duration: 1, // the speed of the animation
                    delay: delay, // time after which the animation starts
                },
                0
            );
        },
        repeatload() {
            let loads = document.querySelectorAll(".loaddot")
            let delay = 0.2
            // console.log(loads,delay)
            loads.forEach((load) => {
                this.ani(load, delay);
                delay += 0.2;
            });
        },
        closeload() {
            setTimeout(() => {
                this.show = false;
            }, 3000);
        }
    }
}
</script>

